@import url(http://fonts.googleapis.com/css?family=Roboto:400,100,100italic,300,300ita‌​lic,400italic,500,500italic,700,700italic,900italic,900);
.view{
    min-height: 100vh;
    position: relative;
}
section{
    margin: 2em 4em;
    padding: 1em;
    background-color: rgb(189 192 192);
    border-radius: .5em;
}
section > *{
    margin: 1em;
    padding: 1em;
    background-color: rgb(230, 230, 231);
    border-radius: .5em;
}
section > *:not(:last-child) {
    margin-bottom: 2em;
}
.form-wrapper {
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    box-sizing: border-box;
    background-color: rgba(0,0,0,0.5);
}

@media screen and (max-width: 800px) {
    section {
        margin: 1em 0;
        padding: .5em;
    }
    section > * {
        margin: .5em;
        padding: .5em;
    }
    section > *:not(:last-child) {
        margin-bottom: .5em;
    }
}

.filter > *{
    margin-right: 1em;
}

li{
    display: -webkit-flex;
    display: flex;
    padding: .5em;
    -webkit-align-items: center;
            align-items: center;
    border: 1px solid rgba(0,0,0,0.1);
    border-radius: 0.5em;
    min-width: 20em;
}
li > *:not(:last-child){
    margin-right: 1em;
}

.icon {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;

    width: 2.5em;
    height: 2.5em;

    background-color: lightgrey;
    border-radius: .5em;
    padding: 0;
    min-width: 2.5em;
    min-height: 2.5em;
}
.operation{
    min-width: 3em;
    min-height: 3em;
}
.operation > *{
    width: 2em;
    height: 2em;
    fill: #424242;
}
.icon-wrapper > *{
    width: 1.5em;
    height: 1.5em;
    fill: #424242;
}

.container {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}
.name, .dateOfCreation{
    width: 5em;
    min-width: 5em;
}
.category{
    width: 4.5em;
    min-width: 4.5em;
}
.number{
    width: 6em;
    min-width: 6em;
}
.text {
    -webkit-flex: 1 1;
            flex: 1 1;
}
@media screen and (max-width: 600px) {
    .primary {
        display: none;
    }
}
@media screen and (max-width: 800px) {
    .secondary {
        display: none;
    }
}
@media screen and (max-width: 1000px) {
    .tertiary {
        display: none;
    }
}

ul {
    max-height: 30em;
    list-style: none;
    overflow-y: auto;
}
ul > *:not(:last-child) {
    margin-bottom: .5em;
}

.middle-block {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: end;
            justify-content: end;
}
.middle-block > * {
    margin-left: 1em;
}
.middle-block div{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    padding: 1em;
    background-color: #d1d2d2;
    border-radius: 0.5em;
}
.announcer {
    opacity:  0;
    transition: opacity 1s;
}
.announcer-error {
    border-color: crimson;
    background-color: rgba(220,20,60,0.5);
}
.announcer-success {
    background-color: #d1d2d2;
}
.visible {
    opacity: 1;
}
.invisible {
    opacity: 0;
}

@media screen and (max-width: 800px) {
    .middle-block div {
        padding: .5em;
    }
}

form *:not(div){
    box-sizing: border-box;
    border: 1px solid rgba(0,0,0,0.1);
}
form{
    width: 20em;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    background-color: white;
    border-radius: 0.5em;
    padding: 1em;
}
form > *:not(:last-child){
    margin-bottom: 0.5em;
}
.button-section {
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}
input, select, textarea{
    width: 100%;
    max-width: 100%;
    padding: 10px;
}
textarea{
    min-width: 100%;
    min-height: 4em;
    max-height: 12em;
}

*{
    color: #424242;
    font-family: 'Roboto', sans-serif;
    margin: 0;
    padding: 0;
}
button:not(.icon){
    font-size: medium;
    padding: .5em 1em;
    background-color: rgb(222, 222, 222);
    border-radius: .5em;
}

@media screen and (max-width: 800px) {
    button:not(.icon){
        font-size: medium;
        font-size: initial;
        padding: .5em;
        background-color: rgb(222, 222, 222);
        border-radius: .5em;
    }
}

